import CONFIG from './config'
import axios from 'axios'
import qs from 'qs'

/*
* 根据
* */
export function searchGoodsList(opts) {
  return axios.post(CONFIG.API.SEARCH_GOODS_LIST, qs.stringify({
    pageIndex:opts.currentPage,
    pageSize:opts.pageSize,
    keywords:opts.itemName
  })).then(res => res.data)
}

/*
* 根据
* */
export function likeGoods(opts) {
  return axios.post(CONFIG.API.LIKE_GOODS, qs.stringify({
    pageIndex:opts.currentPage,
    pageSize:opts.pageSize,
  })).then(res => res.data)
}



