<template>
  <div class="search-detail">
    <goods-search-bar @handleClick="handleClick" :input-disabled="true"
                      @goBack="goBack"
                      placeholder="请输入你喜欢的商品名称" v-model="value">
    </goods-search-bar>
    <scroll :pullup="true"
            @scrollToEnd="loadMore"
            :data="list" class="search-scroll" ref="searchScroll">
      <div>
        <div class="no-search" v-show="noSearch">
          <div class="no-title"><i class="iconfont"
                                   style="font-size: 0.75rem;color: #ff4800">&#xe635;</i>没找到你想要的商品，可以看看下方为你推荐~
          </div>
          <you-like class="you-like"></you-like>
        </div>
        <div class="list-wrapper">
          <div class="list">
            <goods-item @toDetail="toDetail" @loadImage="loadImage" v-for="item in list"
                        :key="item.id" class="list-item"
                        :data="item">
            </goods-item>
          </div>
        </div>
        <loading v-show="showLoad" class="loading"
                 :title="loadingTitle"></loading>
      </div>
    </scroll>
  </div>
</template>

<script>
  import GoodsSearchBar from '../../base/goods-search-bar/goods-search-bar'
  import GoodsItem from '../../base/goods-item/goods-item.vue'
  import Loading from '../../base/loading/loading'
  import Scroll from '../../base/scroll/scroll.vue'
  import YouLike from '../../base/you-like/you-like'
  import * as core from '../../api/search'
  import tool from '../../common/js/util'

  export default {
    name: 'search-details',
    data() {
      return {
        value: '',
        currentPage: 1,
        pageSize: 10,
        list: [],
        loadingTitle: '正在加载...',
        showLoad: true,
        noSearch: false
      }
    },
    created() {
      document.title = this.$route.meta.title     // console.log(this.$route.query.cartIds)
      this.value = this.$route.query.search
      this.getSearch({
        currentPage: this.currentPage,
        pageSize: this.pageSize,
        itemName: this.$route.query.search
      })
    },
    components: {
      GoodsSearchBar,
      GoodsItem,
      Loading,
      Scroll,
      YouLike
    },
    methods: {
      toDetail(e) {
        this.$router.push({path: '/commodityDetails', query: {commodityId: e}})
      },
      loadImage() {
        if (this.timer) {
          clearTimeout(this.timer)
        }
        this.timer = setTimeout(() => {
          this.$refs.searchScroll.refresh()
        }, 500)
      },
      getLikeGoods(opts) {
        this.showLoad = true
        core.likeGoods(opts).then(res => {
          if (res.code && '00' === res.code) {
            this.list = this.list.concat(res.result.data)
            this.loadingTitle = '';
            this.showLoad = false;
            this.currentPage++;
            if (this.list.length >= res.result.amount) {
              this.hasMore = true
            }
          }else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      loadMore() {
        if (this.noSearch) {
          if (!this.showLoad && !this.hasMore) {
            this.getLikeGoods({
              currentPage: this.currentPage,
              pageSize: this.pageSize,
            })
          }
          return false
        } else {
          if (!this.showLoad && !this.hasMore) {
            this.getSearch({
              currentPage: this.currentPage,
              pageSize: this.pageSize,
              itemName: this.$route.query.search
            })
          }
        }

      },
      getSearch(opts) {
        this.showLoad = true
        core.searchGoodsList(opts).then(res => {
          if (res.code && '00' === res.code) {
            if (res.result.amount === 0) {
              this.noSearch = true;
              this.getLikeGoods({
                currentPage: this.currentPage,
                pageSize: this.pageSize
              })
              return false
            }
            this.list = this.list.concat(res.result.data)
            this.loadingTitle = '';
            this.showLoad = false;
            this.currentPage++;
            if (this.list.length >= res.result.amount) {
              this.hasMore = true
            }
          }else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      goBack() {
        this.$router.push({path: '/'})
      },
      handleClick() {
        this.$router.push({
          path: '/search',
          query: {searchWord: this.$route.query.search}
        })
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  .search-detail
    .search-scroll
      position fixed
      top 2.75rem
      left 0
      right 0
      bottom 0
      background-color #f5f5f5
      overflow hidden
      .list-wrapper, .no-search
        width 100%
        padding 0.625rem 3.2% 1rem
        box-sizing border-box
        margin 0 auto
      .no-search
        .no-title
          color #999
          font-size 0.75rem
          padding 1.594rem 0 1rem 0
        .you-like
          margin 1rem 0 0.375rem 0
      .list
        display flex
        justify-content space-between
        flex-wrap wrap
        padding-bottom 1rem
        .list-item
          width 49.43%
          overflow hidden
          border-radius 0.25rem
          margin-bottom 0.25rem
          break-inside: avoid;
</style>



