<template>
  <div class="goods-serach-bar">
    <i style="font-size: 0.875rem;vertical-align: middle" @click="goBack" class="iconfont">&#xe640;</i>
    <div @click="handleClick" class="input-wrapper">
      <i style="font-size: 0.75rem" class="iconfont">&#xe628;</i>
      <input v-focus type="text" @input="$emit('input', $event.target.value)"
             :value="value" :disabled="inputDisabled"
             :placeholder="placeholder" />
    </div>
    <span @click="search" class="search">搜索</span>
  </div>
</template>

<script>
  export default {
    name: 'goods-search-bar',
    props: {
      value: {
        type: String,
        default: ''
      },
      inputDisabled: {
        type: Boolean,
        default: false
      },
      placeholder: {
        type: String,
        default: ''
      }
    },
    methods: {
      search() {
        this.$emit('search')
      },
      goBack() {
        this.$emit('goBack')
      },
      handleClick(){
        this.$emit('handleClick')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/variable.styl"
  @import "../../common/stylus/iconfont.styl"
  @import "../../common/stylus/mixin.styl"
  .goods-serach-bar
    height 2.75rem
    line-height 2.75rem
    border-bottom 1px solid #e6e6e6
    box-sizing border-box
    padding 0 1rem
    display flex
    align-items center
    justify-content space-between
    & > i
      line-height 1
      extend-click()

  .input-wrapper
    width 73.88%
    padding-left 0.938rem
    height 2rem
    line-height 2rem
    border-radius 1rem
    background-color #f5f5f5
    input
      display inline-block
      font-size 0.75rem
      width 85%
      vertical-align middle
      background-color #f5f5f5
      outline none
      line-height 2rem
      &::placeholder
        color #ababab
        font-size 0.75rem

  .search
    extend-click()
    line-height 1
    font-size 0.875rem
    color #666
</style>



